/**=====================
     07. Color CSS Start
==========================**/
.color-box{
  margin-bottom: -10px;
}

// primary Color
$m-val:13;
$or: $primary-color;
@while $m-val >=1 {
  .primary-color ul li:nth-child(#{$m-val}) {
    $or: lighten($or, 1.5%);
    background-color: $or;
    padding: 10px 15px;
    span:before {
      content: "#{$or}";
      display: block;
      color: $white;
      text-align: center;
    }
    $m-val: $m-val - 1;
  }
}
//primary Color

